<template>
    <div>


        <el-drawer title="修改创意" :visible.sync="drawer" :direction="direction" size="60%" >
            <div style="padding: 0 20px; width: 700px;" v-loading="loading">
                <div>
                    <img :src="creative.img_url" width="100" height="100">
                </div>
                

                <el-row>
                    选择创意图片：
                </el-row>
                <el-row>
                    <div class="itemImg">
                        
                        
                        <div v-for="item in itemImg" class="itemImg_item" :class="creative.img_url == item.url ? 'itemImg_active':''">
                            <img :src="item.url" width="100" height="100" @click="setCreativeImg(item.url)" >
                        </div>
                    </div>
                    
                </el-row>
                


                <div>创意标题：</div>
                <el-input v-model="title" maxlength="20" show-word-limit></el-input>

                <span v-for="item in suggestWord" @click="addtoTitle(item[0])" class="suggestword">
                    {{item[0]}}
                </span>
                
                <div v-for="item in magicWord">
                    <span v-for="w in item.data[0]"  class="suggestword" @click="addtoTitle(w.title)">{{w.title}}</span>
                </div>
                

                <div>
                    <el-button type="primary" :loading="applyLoading" style="margin-top: 10px;" @click="applayCreative">立即应用</el-button>
                </div>

            </div>

        </el-drawer>

    </div>



</template>

<script>
    import {
        request
    } from "@/network/request.js";



    export default {
        props: {

        },
        data() {

            return {

                drawer: false, //抽屉状态
                direction: 'rtl', //抽屉开启方向
                applyLoading: false, //加载按钮

                adgroup_id: 0,
                creative: {},
                itemImg: [],
                title:'', //新的创意标题
                
                suggestWord: [],
                magicWord: [], //属性词，扩展
                
                loading:false, 
            };
        },
        methods: {
            //应用修改创意
            applayCreative()
            {
                this.loading=true;
                request({
                    url: "/auto/UpdateCreative",
                    params: {
                        adgroup_id:this.creative.adgroup_id,
                        creative_id:this.creative.creative_id,
                        title:this.title,
                        img_url:this.creative.img_url,
                    },
                    method: 'get',
                }).then(res => {
                    console.log("taobao:", res)
                    
                    this.loading=false;
                    
                    
                    
                    this.$notify({
                              title: '成功',
                              message: '操作成功',
                              type: 'success'
                            });
                            
                    this.drawer=false;
                    
                
                })
                
                
                
                
                
            },
            setCreativeImg(url) {
                this.creative.img_url = url;
            },
            addtoTitle(word) {
                this.title = this.title + word;
            }
        },

        mounted() {

            //https://suggest.taobao.com/sug?code=utf-8&q=%E8%BF%9E%E8%A1%A3%E8%A3%99&area=c2c
            
            this.title = this.creative.title;

            request({
                url: "/auto/GetTaobaoSuggestWord",
                params: {},
                method: 'get',
            }).then(res => {
                console.log("taobao:", res)
                this.suggestWord = res.data.result;
                this.magicWord = res.data.magic;
                

            })

        },
        computed: {


        },
        watch: {}

    };
</script>

<style>
    .itemImg {
        display: flex;
        justify-content: left;
        
        
    }
    .itemImg div {
        margin-right: 20px;
    }

    .itemImg_active {
        border: 2px solid #ff5500;
    }
    
    .suggestword {
        background-color: rgb(250, 236, 216);
        
        border:1px solid #E6A23C;
        border-radius: 5px;
        margin-right: 10px;
        padding:5px;
        line-height: 30px;
        cursor: pointer;
        
    }
    .el-drawer__body {
        overflow: auto;
        /* overflow-x: auto; */
    }
</style>
