<template>
    <div class="main-content">
        <el-page-header @back="goBack" content="查看计划">
        </el-page-header>
        
        
        <div class="top-step">
            <el-steps :active="1" style="width:30%;">
              <el-step title="计划设置" description=""></el-step>
              <el-step title="宝贝设置" description=""></el-step>
            </el-steps>
        </div>
        
        
        
        
        <el-container>
            

            
        <el-aside width="500px" >
            <div>
                <el-button type="primary" @click="handleChooseGoods"> 添加商品 </el-button>
            </div>
            <div v-for="(item,index) in adgroup" :key="item.num_iid" class="adgroup-item" :class="adgroup_active==item? 'adgroup-active':''" >
                  <img :src="item.img_url" width="100" height="100" @click="showAdgroup(item)">
              <div class="item_title" @click="showAdgroup(item)">{{ item.title }} <br />{{item.adgroup_id}}</div>
              <div><el-button @click="handleDeleteAdgroup(item,index)"  size="mini" icon="el-icon-delete" circle> </el-button></div>
            </div>
            <div v-if="!adgroup">没有数据</div>
            
            
        </el-aside>
        <el-main v-if="adgroup">

            <el-tabs v-model="activeName">
              <el-tab-pane label="关键词" name="first"></el-tab-pane>
              <el-tab-pane label="创意" name="second"></el-tab-pane>
            </el-tabs>
            
            <div v-loading="loading">
                <transition
                enter-active-class="animated fadeIn"    
                      >
                      <div v-if="activeName == 'first'">
                          
                          <el-button type="primary" @click="handleInitKeywords" >初始化关键词</el-button>
                          <div class="keywords-div">
                              <div v-for="(item,index) in keywords" >
                                  {{item.word}}  出价：{{item.max_price}}  <el-button @click="handleDeleteKeyword(item,index)" icon="el-icon-delete" size="mini" circle> </el-button>
                              </div>
                          </div>
                          
                      </div>
                      
                      </transition>
                
                
                      <transition
                      enter-active-class="animated fadeIn"
                            >
                      <div v-if="activeName == 'second'">
                          <el-button type="primary" @click="handleInitCreative">初始化创意</el-button>
                          <div v-for="(item,index) in creatives">
                              
                              <el-row>
                              <el-col :span="5">
                                  <img :src="item.img_url" width="100" height="100">
                              </el-col>
                              <el-col :span="14">
                                   {{item.title}}<br />
                                   {{item.price}}
                              </el-col>
                              <el-col :span="5">
                                  <el-button @click="handleSetCreative(item)" icon="el-icon-edit" size="mini" circle> </el-button>
                                  
                                  <el-button @click="handleDeleteCreative(item,index)" icon="el-icon-delete" size="mini" circle style="margin-left:5px;"> </el-button>
                                  
                                  
                              </el-col>
                              </el-row>
                              
                          </div>
                          
                
                      </div>
                      </transition>
                
            </div>
                  
                  
            
        </el-main>
        
        
        </el-container>
        

        
        <ChooseGoodsList ref="DrawerChooseGoodsList" v-if="showChooseGoods" @ChoosedGoodsList='ChoosedGoodsList'></ChooseGoodsList>
        
        <setCreative ref="DrawerSetCreative"></setCreative>
        
        
        

    </div>
</template>

<script>
    import ChooseGoodsList from "@/components/commen/ChooseGoodsList.vue"
    import addKeywordsList from "@/components/commen/addKeywordsList.vue"
    import setCreative from "@/components/commen/setCreative.vue"

    import {request} from "@/network/request.js";
    


    export default {
        data() {
            return {
                campaign_id:0,
                campaign:{},//当前推广计划
                adgroup:[], //推广组数组
                adgroup_active:[],//选中的推广组
                isCollapse: false,
                activeName: 'first',
                num_iid:0,
                itemImg:[],
                keywords:[],
                creatives:[],
                textarea2: '',
                showChooseGoods:false, //显示商品选择模块
                loading:false,
                visible:false, //确认对话框
                
            }

       },
        components:{
            ChooseGoodsList:ChooseGoodsList,
            addKeywordsList:addKeywordsList,
            setCreative:setCreative
        },
        watch: {
        },
        methods: {
            handleChooseGoods()
            {
                this.showChooseGoods=true;
                
                //去除已经添加过的
                let num_iids=[];
                if(this.adgroup)
                {
                    
                    this.adgroup.forEach(item=>{
                        num_iids.push(item.num_iid);
                    })
                }
                
                this.$nextTick(()=>{
                    this.$refs.DrawerChooseGoodsList.num_iids = num_iids;      
                    this.$refs.DrawerChooseGoodsList.drawer = true;
                })
                
                
            },
            handleSetCreative(item)
            {
                this.$refs.DrawerSetCreative.drawer = true;
                this.$refs.DrawerSetCreative.creative = item;
                this.$refs.DrawerSetCreative.title = item.title;
                
                this.$refs.DrawerSetCreative.itemImg = this.itemImg;
            },
            
            
            //删除创意
            handleDeleteAdgroup(item,index)
            {
                
                console.log(item);
                
                this.$confirm('确定删除?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            request({
                                url: "/auto/AdgroupDelete",
                                params:{
                                    adgroup_id:item.adgroup_id,
                                },
                                method: 'get',
                            }).then(res => {
                                console.log(res)
                                //this.data = res.data.data.items.item;
                                this.$notify({
                                          title: '成功',
                                          message: '操作成功',
                                          type: 'success'
                                });
                                
                                
                                this.adgroup.splice(index,1);
                                
                                let showindex = (index-1 >=0) ? index-1: 0;
                                
                                
                                this.showAdgroup(this.adgroup[showindex]);
                                
                                
                            })
                            
                            
                })
                
            },
            
            
            //删除创意
            handleDeleteCreative(item,index)
            {
                
                console.log(item);
                
                this.$confirm('确定删除?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            request({
                                url: "/auto/CreativeDelete",
                                params:{
                                    creative_id:item.creative_id,
                                },
                                method: 'get',
                            }).then(res => {
                                console.log(res)
                                //this.data = res.data.data.items.item;
                                this.$notify({
                                          title: '成功',
                                          message: '操作成功',
                                          type: 'success'
                                });
                                
                                
                                this.creatives.splice(index,1);
                                
                                
                            })
                            
                            
                })
                
            },
            //删除关键词
            handleDeleteKeyword(item,index)
            {
                console.log(item);
                
                this.$confirm('确定删除?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            request({
                                url: "/auto/KeywordDelete",
                                params:{
                                    campaign_id:item.campaign_id,
                                    keyword_ids:item.keyword_id
                                },
                                method: 'get',
                            }).then(res => {
                                console.log(res)
                                //this.data = res.data.data.items.item;
                                this.$notify({
                                          title: '成功',
                                          message: '操作成功',
                                          type: 'success'
                                });
                                
                                // this.showAdgroup(this.adgroup_active);
                                
                                this.keywords.splice(index,1);
                                
                                
                            })
                            
                            
                })
            },
            
            //点击初始化关键词
            handleInitKeywords()
            {
                this.$confirm('确定要初始化关键词?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            request({
                                url: "/auto/InitKeywords",
                                params:{
                                    adgroup_id:this.adgroup_active.adgroup_id,
                                },
                                method: 'get',
                            }).then(res => {
                                console.log(res)
                                //this.data = res.data.data.items.item;
                                this.$notify({
                                          title: '成功',
                                          message: '操作成功',
                                          type: 'success'
                                        });
                                        
                                this.showAdgroup(this.adgroup_active);
                            })
                            
                            
                })
                
            },            
            handleInitCreative()
            {
                this.$confirm('确定要初始化创意?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            request({
                                url: "/auto/InitCreative",
                                params:this.adgroup_active,
                            }).then(res => {
                                console.log(res)
                                //this.data = res.data.data.items.item;
                                this.$notify({
                                          title: '成功',
                                          message: '操作成功',
                                          type: 'success'
                                        });
                                this.showAdgroup(this.adgroup_active);
                            })
                            
                            
                            
                        })

                
            },
            handleInitCampaign()
            {
                
                request({
                    url: "/auto/InitCampaign",
                    params:{
                        campaign_id:this.campaign.campaign_id,
                    },
                    method: 'get',
                }).then(res => {
                    console.log(res)
                    //this.data = res.data.data.items.item;
                    this.$notify({
                              title: '成功',
                              message: '操作成功',
                              type: 'success'
                            });
                            
                            
                    
                })
                
                
            },
            //点击推广组，显示创意和关键词
            showAdgroup(adgroup){
                
                console.log(adgroup);

                this.adgroup_active = adgroup;                
                this.keywords = [];
                this.creatives = [];
                
                
                if(!adgroup.adgroup_id)
                return;
                
                this.loading=true;
                
                var count = 1;

                //获取关键词
                request({
                    url: "/auto/KeywordsbyadgroupidGet",
                    params:{
                        adgroup_id:adgroup.adgroup_id
                    },
                    method: 'get',
                }).then(res => {
                    console.log('adgroup_keyword:',res)
                    //this.data = res.data.data.items.item;
                    this.keywords = res.data.keywords.keyword;
                    
                    count --;
                    
                })
                
                //获取创意
                request({
                    url: "/auto/CreativesGet",
                    params:{
                        adgroup_id:adgroup.adgroup_id
                    },
                    method: 'get',
                }).then(res => {
                    console.log('actionCreativesGet:',res)
                    //this.data = res.data.data.items.item;                    
                    this.creatives = res.data.creatives.creative;
                    count --;

                })
                
                var timer = setInterval(()=>{
                    
                    console.log(count);
                    
                    if(count <0)
                    {
                        this.loading=false;
                        clearInterval(timer);
                    }
                    
                    
                },500);
                

                this.ItemSellerGet(adgroup.num_iid);

                
            },
            handleCloseMobilePrice()
            {
                
                let adgroup_ids =  '';
                this.adgroup.forEach(item =>{
                    adgroup_ids += item.adgroup_id+',';
                })


                
                request({
                    url: "/auto/CloseMobilePrice",
                    params:{
                        adgroup_ids,
                    },
                    method: 'get',
                }).then(res => {
                    console.log(res)
                    //this.data = res.data.data.items.item;
                    alert ('关闭移动溢价成功')
                })
            },
            
            //获取推广组列表显示出来
            AdgroupsbycampaignidGet()
            {
                
                request({
                    url: "/auto/AdgroupsbycampaignidGet",
                    params:{
                        campaign_id:this.campaign.campaign_id,
                    },
                    method: 'get',
                }).then(res => {
                    console.log('campaign_get:',res)
                    //this.data = res.data.data.items.item;
                    
                    this.adgroup = res.data.adgroups.adgroup_list.a_d_group;
                    
                    this.showAdgroup(this.adgroup[0]);
                    
                    
                })
            },
            //在选择宝贝页面选择完成宝贝
            
            ChoosedGoodsList(res)
            {
                console.log(res);
                
                this.$refs.DrawerChooseGoodsList.loading = true;
                
                let count = res.length;
                
                
                
                res.forEach(item=>{
                    
                    this.$refs.DrawerChooseGoodsList.loadingText = "正在添加"+item.title;
                    
                    request({
                        url: "/auto/AdgroupAdd",
                        params:{
                            campaign_id:this.campaign.campaign_id,
                            goods:item,
                        },
                    }).then(res => {
                        console.log('adgroupadd:',res)
                        
                        if(res.rtn==0)
                        {
                            this.$message({
                                message: item.num_iid+ '添加成功！',
                                type: 'success'
                            });
                        } else {
                            this.$notify({
                                      title: '提示',
                                      message: item.num_iid+ '添加失败！'+res.msg,
                                      duration: 0
                                    });
                        }
                        
                        count --;
                        
                        console.log(count);
                        
                        
                        
                        if(count == 0)
                        {
                            //重新获取推广组
                            this.AdgroupsbycampaignidGet();                            
                            //关闭加载
                            this.$refs.DrawerChooseGoodsList.loading = false;                            
                            //关闭选择商品窗口
                            this.$refs.DrawerChooseGoodsList.drawer = false;
                            
                        }

                        
                    })
                    
                })

            },
            

            AddKeywords()
            {
                request({
                    url: "/auto/KeywordsvonAdd",
                    params:{
                        adgroup_id:this.adgroup_id,
                    },
                    method: 'get',
                }).then(res => {
                    console.log('add keyword:',res)
            
                    
                })
            },
            //获取商品详细信息中的多张图片
            ItemSellerGet(num_iid)
            {
                request({
                    url: "/auto/ItemSellerGet",
                    params:{
                        num_iid:num_iid,
                    },
                    method: 'get',
                }).then(res => {
                    console.log('ItemSellerGet:',res)
                    this.itemImg = res.data.item.item_imgs.item_img;
                    
                })
            },
            goBack()
            {
                this.$router.back();
            },
                  handleOpen(key, keyPath) {
                    console.log(key, keyPath);
                  },
                  handleClose(key, keyPath) {
                    console.log(key, keyPath);
                  }
            

        },
        mounted() {
            this.campaign = this.$route.query;
            this.AdgroupsbycampaignidGet();
            
            
            
            
        }
    }
</script>

<style scoped="">
      .el-menu-vertical-demo:not(.el-menu--collapse) {
        min-height: 400px;
        
      }
    .main-content{
        width:100%;
                font-size: 13px;

    }
    .adgroup-item {
        display: flex;
        padding: 15px;
        border-bottom: 1px solid #EEEEEE;

        cursor: pointer;
    }
    .item_img_url {
        padding:0 10px;
        flex:1;
        }
    .item_title {
        flex: 5;
        font-size: 0.8rem;
        margin: 0 10px;
    }
    .item_price {
        display: block;
    }
    .el-menu-item a{
        word-break: break-all;
        word-break: break-word;
    }
    .top-step{
        display: flex;
        justify-content: center;
    }
    .adgroup-active {
        border-left:1px solid #409EFF;
        border-top:1px solid #409EFF;
        border-bottom:1px solid #409EFF;
        border-right: 1px solid #FFFFFF;
        z-index: 9999;
    }
    .el-button{
        margin:5px 0;
    }
    .el-main{
        border: 1px solid #409EFF;margin-top: 50px;
    }
    .keywords-div {
        line-height: 1.5rem;
        }

</style>
