<template>
    <div>


        <el-drawer title="选择商品" :visible.sync="drawer" :direction="direction" size="60%" >
            <div style="padding: 0 20px;" v-loading="loading" :element-loading-text="loadingText">
                <div class="category-search">
                    
                      <el-select v-model="CategorySelect" placeholder="全部类目">
                        <el-option
                          v-for="item in Category.seller_cat"
                          :key="item.cid"
                          :label="item.name"
                          :value="item.cid">
                        </el-option>
                      </el-select>
                      
                      
                      <el-input placeholder="宝贝关键词" v-model="GoodsKeyWord" clearable class="input-with-select">
                        <el-button slot="append" icon="el-icon-search" @click="SearchGoods"></el-button>
                      </el-input>
                      

                </div>
                <div class="el-transfer">
                  <template>
                    <div class="my-panel el-transfer-panel_left">
                        <div class="item_header">
                            <div class="item_title">商品</div>
							<div class="item_quantity" @click="orderBy('list_time')">发布时间</div>
                            <div class="item_quantity" @click="orderBy('sold_quantity')">销量<i :class="orderby.sold_quantity?'el-icon-bottom':'el-icon-top'"></i></div>
                            <div  class="item_quantity"  @click="orderBy('num')">库存<i :class="orderby.num?'el-icon-bottom':'el-icon-top'"></i></div>
                       </div>
                       
                       <el-checkbox-group
                         v-model="leftChecked"
                           :max="max"
                          >
                       <div class="infinite-list" v-infinite-scroll="loadMoreGoods">
                           <!-- <div v-for="(item,index) in data">{{index}}{{item.title}}</div> -->
                       
                        
                          
                          <div class="my-panel-item" v-for="item in data">
                              <el-checkbox
                              :label="item"
                              :key="item.num_iid"
                              class="item_checkbox"
                              >
                              </el-checkbox>
                                  <div class="item_pic_url"><img :src="item.pic_url" width="60" height="60" /></div>
                                  <div class="item_title">{{ item.title }}<span class="item_price">{{ item.price }}</span></div>
                                  <div class="item_quantity">{{ item.list_time }}</div>
                                  <div class="item_quantity">{{ item.sold_quantity }}</div>
                                  <div class="item_num">{{ item.num }}</div>
                                  
                                  
                          </div>
                        
                        
                        </div>
                        </el-checkbox-group>
                
                      </div>
                  </template>
                  
                  <template>
                    <div class="my-panel el-transfer-panel_right">
                        
                        <div class="item_header">
                             <div>已选择 {{leftChecked.length}} 个宝贝</div> <div @click="removeAll">全部移除</div>
                        </div>
                        
                        
                        <div  class="my-panel-list"> 
                        
                        <div v-for="(item,index) in leftChecked" :key="item.num_iid">
                
                            <span  class="my-panel-item">
                                <div class="item_pic_url"><img :src="item.pic_url" width="60" height="60" /></div>
                                <div class="item_title">{{ item.title }}<span class="item_price">{{ item.price }}</span></div>
                                <div class="item_handle" @click="deleteItem(index)">删除</div>
                            </span>
                        </div>
                      </div>
                
                  
                      </div>
                  </template>
                
                
                </div>
                
  
                
                <div>
                    <el-button type="primary" :loading="applyLoading" style="margin-top: 10px;" @click="ChoosedGoodsList">立即应用</el-button>
                </div>

            </div>
        </el-drawer>
        
    </div>



</template>

<script>
    import {request} from "@/network/request.js";
    
    
    
    export default {
        props: {
            
            adgroup:{
                type:Array
            }

        },
        data() {
                  
            return {
                data: [],
                value: [],
                value4: [],
                max:5,
                renderFunc(h, option) {
                    return "<span>WTF{ option.title }</span>";
                },
                drawer: false, //抽屉状态
                direction: 'rtl', //抽屉开启方向
                applyLoading: false, //加载按钮
                Category:[],//商品类目
                CategorySelect:'',
                GoodsKeyWord:'',//商品搜索关键字
                orderbyNum: true,
                orderbySoldQuantity:true,
				orderby:{num:0,sold_quantity:0},
                leftChecked:[],
                rightChecked: [],
                checked: [],
                num_iids:[],//已存在num_ids
                page_no:1, //商品列表默认页
                
                loading:false, 
                loadingText:"加载中",
                
            };
        },
        methods: {
            handleChange(value, direction, movedKeys) {
                console.log(value, direction, movedKeys);
            },
            mycheck(val)
            {
                console.log("mycheck",val);
                this.value4.concat(val);
            },
            GetItemList()
            {
                request({
                    url: "/auto/ItemsOnsaleGet",
                    params:{
                        q:this.GoodsKeyWord,
                        seller_cids:this.CategorySelect,
                        page_no:this.page_no,
                    },
                    method: 'get',
                }).then(res => {
                    console.log(res)

                    let items = res.data.data.items.item;
                    
                    for(var i=0;i<items.length-1;i++)
                    {
                        console.log(this.num_iids.indexOf(items[i].num_iid))
                        if(this.num_iids.indexOf(items[i].num_iid) > -1 ){
                            
                            //console.log(items[i].num_iid+'重复')
                            items.splice(i,1);
                            i--; //如果不减，会漏掉一个
                        }

                    }
                    
                    this.data = [...this.data,...items];
                    
                    console.log(this.data);
                    
                })
            },
            loadMoreGoods()
            {
                console.log('load more...');
                
                this.page_no ++ ;
                this.GetItemList();
            },
            //选择类目后，点击搜索
            SearchGoods()
            {
                console.log('search goods...');
                
                this.page_no =1 ;
                this.data=[];
                this.GetItemList();
            },
            deleteItem(index){
                this.leftChecked.splice(index,1)
            },
            removeAll()
            {
                this.leftChecked=[];
            },
            handleCheckedChange(e)
            {
                alert('afa')
                console.log(e)
                e.checked=false;
            },
            //选择完成
            ChoosedGoodsList()
            {
                this.$emit('ChoosedGoodsList',this.leftChecked);
            },
			orderBy(type)
			{
				console.log(type)

				function ascend(x,y){
					return x[type] - y[type];  //按照数组的第4个值升序排列
				}
				function descend(x,y){
					return y[type] - x[type];  //按照数组的第1个值升序排列
				}
									
				if(this.orderby[type])
				{
					this.data.sort(ascend);
					
				} else {
					this.data.sort(descend);
				}
				this.orderby[type] = ! this.orderby[type]
				
			}
        },
        
        mounted() {
            
            //生成已添加到的商品数组，方便从商品列表中去除
            this.GetItemList();
            
            request({
                url: "/auto/ShopCatsListGet",
                method: 'get',
            }).then(res => {
                console.log(res)
                this.Category = res.data.data.seller_cats;
            })
            
            
            
            this.$on('mychange1',item=>{
                
                console.log(item);
                
                this.value4.concat(item.num_iid)
                
                
            })
        },
        computed: {

      
        },
        watch:{
            leftChecked(){
                if(this.leftChecked.length>2)
                {
                    this.$notify.error({
                      title: '错误',
                      message: '最多只能添加5个'
                    });
                    return;
                }
            }
        }

    };
</script>

